// Here You can style and customize the home page:

@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

// <> How to organize the CSS properties:
// 1 - >> display stuff:
// 2 - >> positioning stuff:
// 3 - >> box-model stuff:
// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// start header //
header {
  @extend %header;
}
/* End header */
/* Start Landing Section */
.landing {
  position: relative;
  display: block;
  height: 380px;
  background-image: url("../images/mobile-landing.png");
  background-size: cover;
  background-position: top center;
  @include breakpoint(laptop) {
    min-height: 100vh;
    background-image: url("../images/landing.png");
  }
  img {
    display: none;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -55%);
    @include breakpoint(laptop) {
      display: block;
    }
  }
}

/* End Landing Section */

/* Start Contact Section */

.contact {
  padding-top: $section-padding-md ;
  text-align: center;
  @include breakpoint(laptop) {
    background-image: url("../images/contact-bg.png");
    background-position: top center;
    background-size: cover;
    height: 497px;
    position: relative;
    top: -115px;
  }
  h2 {
    @extend %section_header-4;
  }
  p {
    font-size: .875rem;
    padding-top: $txt-padding-md;
    color: var(--gray-light-clr);
    span {
      display: none;
    }
  }
  .social-icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: $txt-margin-md auto;
    gap: $txt-padding-sm;
    max-width: 600px;
    a {
      width: 3rem;
      height: 3rem;
      img {
        transition: $transition;
      }
      &:hover img {
        transform: translateY(-.5rem);
        filter: saturate(1.5);
      }
    }
  }
  @include breakpoint(tablet) {
    p {
      font-size: 1rem;
      span {
        display: inline-block;
      }
    }
    .social-icons {
      a {
        width: 4rem;
        height: 4em;
      }
    }
  }
}

/* End Contact Section */

/* Start features Section */

.features {
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  @include breakpoint(laptop) {
    padding-top: 0;
  }
  h2 {
    @extend %section_header-4;
  }
  .features-list {
    display: grid;
    gap: $txt-padding-lg;
    margin-top: $txt-margin-lg;
    @include breakpoint(laptop) {
      grid-template-columns: repeat(3, minmax(275px, 1fr));
    }
    .feature-item {
      position: relative;
      text-align: center;
      padding: $txt-padding-lg $txt-padding-md;
      border-radius: $border-radius-lg;
      border: 1px solid var(--transparent-black-clr);
      box-shadow: $box-shadow;
      &:hover {
        border-color: var(--primary-light-clr);
        background-color: var(--primary-light-clr);
        img:first-child {
          animation: scale 0.4s ease-in-out alternate;
        }
        img {
          filter: grayscale(0);
        }
        h3, p {
          color: var(--white-clr);
        }
      }
      img {
        filter: grayscale(100%);
      }
      img:first-child {
        margin-bottom: $txt-padding-sm;
      }
      img:last-child{
        position: absolute;
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%);
      }
      h3 {
        padding-bottom: $txt-padding-md;
        text-transform: capitalize;
        color: var(--purple-dark-clr);
      }
      p {
        font-size: .875rem;
        line-height: $line-height-lg;
        color: var(--gray-dark-clr);
      }
    }
  }
}

/* End features Section */

/* Start Video Section */

.video {
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  background-image: url("../images/video-bg.png");
  background-position: top center;
  background-size: cover;
  min-height: 790px;
  .container {
    position: relative;
    padding-top: $section-padding-md ;
    padding-bottom: $section-padding-md ;
    .img-holder{
      position: relative;
      top: 20px;
      height: 360px;
      border-radius: $border-radius-lg;
      overflow: hidden;
      background-image: url("../images/video.png");
      background-position: top center;
      background-size: cover;
      @include breakpoint(laptop) {
        max-width: 450px;
        top: 0;
        left: $txt-margin-lg;
        z-index: 2;
      }
      .video-play-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 3;
        transform: translate(-50%, -50%);
        &::after {
          content: "";
          left: 0;
        }
        @include pulse;
      }
      svg {
        cursor: pointer;
        border-radius: $circle;
        padding: 1.125rem $txt-margin-md;
        color: var(--primary-dark-clr);
        background-color: var(--white-clr);
      }
    }
    .text{
      text-align: center;
      padding: $txt-margin-lg 0 $txt-margin-md;
      border-radius: $border-radius-lg;
      background-color: var(--primary-dark-clr);
      @include breakpoint(laptop) {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, -50%);
        text-align: right;
        padding: 4.375rem $txt-margin-lg;
      }
      @include breakpoint(desktop) {
        padding: 4.375rem $txt-margin-xlg;
      }
      p {
        display: inline-block;
        font-size: .875rem;
        letter-spacing: -0.5px;
        color: var(--white-clr);
        @include breakpoint(tablet) {
          font-size: 20px;
        }
        @include breakpoint(desktop) {
          font-size: 25px;;
        }
      }
      img {
        display: inline-block;
        transition: $transition;
        object-fit: cover;
      }
      @include breakpoint(desktop) {
        &:hover img {
          animation: round 2s ease-in-out alternate;
        }
      }
    }
    video {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 90%;
      z-index: 12;
      border-radius: $border-radius;
      @include breakpoint(laptop) {
        max-width: 50%;
      }
    }
  }
}

/* End Video Section */

/* Start About Us Section */
.about-us {
  position: relative;
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  @include breakpoint(laptop) {
    margin-bottom: 300px;
    &::before {
      content: "";
      position: absolute;
      top: 150px;
      left: 50%;
      transform: translateX(-50%);
      width: 3px;
      height: 115%;
      background: linear-gradient(to bottom, var(--orange-light-clr), transparent);
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -280px;
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      height: 47%;
      margin-left: 0.5px;
      border: 1px dashed var(--orange-light-clr);
    }
    .container {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        top: 90px;
        left: 50%;
        transform: translateX(-50%);
        width: 10px;
        height: 10px;
        border-radius: $circle;
        box-shadow: $box-shadow-down;
        border: 3px solid var(--white-clr);
        background-color: var(--white-light-clr);
      }
    }
  }
  h2 {
    @extend %section_header-5;
  }
  .time-line {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(325px, 1fr));
    gap: $txt-padding-xlg;
    margin-top: $txt-margin-xlg;
    @include breakpoint(laptop) {
      grid-template-columns: repeat(2, calc(50% - $txt-padding-xlg));
      justify-content: space-between;
      column-gap:0;
    }
    .time-line-item {
      position: relative !important;
      text-align: center;
      box-shadow: $box-shadow;
      border-radius: $border-radius-lg$border-radius-lg 0 0;
      .img-holder {
        display: flex;
        align-items: center;
        font-weight: 800;
        overflow: hidden;
        justify-content: space-between;
        padding: $txt-padding-md $txt-padding-sm;
        border-radius: $border-radius-lg $border-radius-lg 0 0;
        @include breakpoint(tablet) {
          padding-left: $txt-padding-lg;
          padding-right: $txt-padding-lg;
        }
        span {
          font-size: 5.25rem;
          letter-spacing: 3px;
          color: var(--transparent-white-clr);
        }
      }
      > span {
        position: relative;
        top: -25px;
        font-weight: bold;
        font-size: 1rem;
        padding: $txt-margin-md;
        text-transform: capitalize;
        border-radius: $border-radius-lg;
        background-color: var(--white-clr);
        font-family: 'Barlow Condensed', sans-serif;
        span {
          font-weight: 900;
          font-size: 2rem;
          letter-spacing: -1px;
        }
      }
      p {
        font-size: .8125rem;
        line-height: $line-height-lg;
        color: var(--purple-light-clr);
        padding: 0 $txt-padding-sm $txt-padding-lg;
      }
    }
    .time-line-item:first-of-type .img-holder {
      background-color: var( --blue-dark-clr);
    }
    .time-line-item:nth-of-type(2) .img-holder {
      background-color: var(--green-light-clr);
    }
    .time-line-item:nth-of-type(3) .img-holder {
      background-color: var(--orange-light-clr);
      & img {
          position: relative;
          top: -20px;
      }
    }
    .time-line-item:last-of-type .img-holder {
      padding-top: 40px;
      background-color: var(--purple-dark-clr);
      & img {
        position: relative;
        bottom: -30px;
      }
    }
    @include breakpoint(laptop) {
      .time-line-item:first-of-type,
      .time-line-item:nth-of-type(2),
      .time-line-item:nth-of-type(3),
      .time-line-item:last-of-type {
        &::before {
          content: "";
          position: absolute;
          top: 160px;
          width: 6px;
          height: 6px;
          border-radius: $circle;
          box-shadow: $box-shadow-down;
          z-index: 2;
          border: 3px solid var(--white-clr);
          background-color: var(--white-light-clr);
        }
        &::after {
          content: "";
          position: absolute;
          top: 165px;
          width: 85px;
          height: 0;
          z-index: 2;
          border: 1px dashed var(--orange-light-clr);
          background-color: var(--white-light-clr);
        }
      }
      .time-line-item:first-of-type,
      .time-line-item:nth-of-type(3) {
        &::before {
          right: -107px;
          background-color: var(--orange-light-clr);
        }
        &::after {
          right: -92px;
        }
      }
      .time-line-item:nth-of-type(2), 
      .time-line-item:last-of-type {
        &::before {
          left: -104.5px;
        }
        &::after {
          left: -90px;
        }
      }
      .time-line-item:nth-of-type(2) {
        bottom: -175px;
      }
      .time-line-item:last-of-type {
        bottom: -175px;
        .img-holder {
          padding-right: 0;
        }
      }
    }
  }
  .container > a {
    position: absolute;
    bottom: -100px;
    left: 50%;
    width: 60px;
    height: 60px;
    line-height: 16px;
    text-align: center;
    transition: $transition;
    transform: translateX(-50%);
    border-radius: $circle;
    color: var(--white-clr);
    box-shadow: $box-shadow-around;
    border: 20px solid var(--white-clr);
    background-color: var(--orange-light-clr);
    @include breakpoint(laptop) {
      bottom: -390px;
    }
    svg {
      font-size: 12px;
      transform-origin: center;
    }
    &:hover {
      transform: translateX(-50%) rotate(180deg);
    }
  }
}

/* End About Us Section*/

/* Start office section */
.office {
  .curly-bg:first-child,
  .curly-bg:last-child {
    height: 145px;;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
  }
  .curly-bg:first-child {
    background-image: url("../images/curly-bg-1.png");
  }
  .curly-bg:last-child {
    background-image: url("../images/curly-bg-2.png");
  }
  .bg-wrapper {
    padding-top: $section-padding-md;
    background-color: var(--gray-clr);
  }
  h2 {
    @extend %section_header-5;
  }
  p {
    text-align: center;
    color: var(--gray-dark-clr);
  }
  .gallery {
    margin-top: $txt-margin-lg;
    @include breakpoint(phone) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $txt-padding-lg;
    }
    @include breakpoint(desktop) {
      grid-template-columns: repeat(4, 1fr);
    }
    .image {
      position: relative;
      overflow: hidden;
      border-radius: $border-radius;
      padding-bottom: $txt-margin-md;
      a {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        transform: translate(-50%, -50%);
        cursor: pointer;
        opacity: 0;
        transition: $transition;
        border-radius: $circle;
        color: var(--orange-light-clr);
        background-color: var(--white-clr);
      }
      &:hover {
        a {
          opacity: 1;
        }
      }
    }
  }
}

/* End office section */

/* Start .our-work section */

.our-work {
  padding-top: $section-padding-md;
  padding-bottom: $section-padding-md;
  h2 {
    @extend %section_header-5;
  }
  .slider {
    position: relative;
    height: 450px;
    margin-top: $txt-margin-lg;
    background-image: linear-gradient(to top, #6a002ecc, #fb6431cc);
    .slide {
      height: 100%;
      width: 100%;
      position: absolute;
      overflow: hidden;
      clip-path: circle(0% at 0 50%);
      &.active {
        transition: $transition;
        clip-path: circle(150% at 0 0%);
      }
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .info {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        bottom: 0;
        text-align: center;
        padding: $txt-padding-lg $txt-margin-md;
        color: var(--black-dark-clr);
        box-shadow: $box-shadow-light;
        background-image: linear-gradient(to top, #6a002ecc, #fb6431cc);
        @include breakpoint(phone) {
          height: 70%;
        }
        @include breakpoint(tablet) {
          padding: $txt-margin-lg $txt-margin-md;
          height: 100%;
          width: 50%;
        }
        @include breakpoint(laptop) {
          width: 35%;
        }
        h2 {
          color: var(--white-clr);
        }
        p {
          color: var(--white-dark-clr);
        }
        a {
          display: inline-block;
          min-width: 188px;
          line-height: $line-height-lg;
          border-radius: 27px;
          text-transform: uppercase;
          background-color: transparent;
          transition: $transition;
          color: var(--white-clr);
          margin: 35px auto 0 !important;
          border: 1px solid var(--transparent-white-clr);
          &:hover {
            background-color: var(--transparent-white-clr);
          }
        }
      }
    }
    .slider-controls {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: absolute;
      bottom: -23px;
      left: 50%;
      transform: translateX(-50%);
      min-width: 280px;
      text-align: center;
      border-radius: 27px;
      box-shadow: $box-shadow-down;
      padding: $txt-padding-md;
      background-color: var(--white-clr);
      @include breakpoint(tablet) {
        bottom: 50px;
        left: 25%;
      }
      @include breakpoint(laptop) {
        left: 17%;
      }
      .prev-btn, .next-btn {
        font-size: 18px;
        cursor: pointer;
        color: var(--primary-clr);
      }
      .indicators {
        li {
          display: inline-block;
          width: 12px;
          height: 12px;
          border: 1px solid transparent;
          border-radius: $circle;
          cursor: pointer;
          transition: $transition;
          margin: 0 $txt-margin-sm;
          background-color: var(--orange-dark-clr);
          &.active,
          &:hover,
          &:focus {
            border-color: var(--gray-dark-clr);
            background-color: transparent;
          }
        }
      }
    }
  }
}

/* End our-work section */

/* Start initiatives Section */

.initiatives {
  min-height: 400px;
  padding-top: $section-padding-md;
  padding-bottom: $section-padding-md;
  background-image: url("../images/initiatives-bg.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  h2 {
    text-align: center;
    text-transform: uppercase;
    color : var(--blue-light-clr);
    margin-bottom: 8px;
  }
  .container > img {
    display: block;
    margin: 0 auto;
  }
  .images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: $txt-margin-md;
    margin-top: $txt-margin-md;
    @include breakpoint(tablet) {
      grid-template-columns: repeat(4, 1fr);
    }
    a {
      margin: 0 auto;
      width: fit-content;
      transition: $transition;
      &:hover {
        transform: translateY(-8px);
      }
    }
  }
}

/* End initiatives Section */

/* End offer Section */

.offer {
  padding-top: $section-padding-md;
  h2 {
    font-size: 25px;
    text-align: center;
    font-weight: 300;
    text-transform: capitalize;
    color: var(--gray-dark-clr);
  }
  p {
    text-align: center;
    line-height: $line-height;
    font-weight: 300;
    font-family: 'Barlow Condensed', sans-serif;
    color: var(--blue-light-clr);
    span {
      color: var(--orange-light-clr);
    }
  }
  a {
    display: block;
    width: 190px;
    height: 48px;
    margin: 20px auto 30px;
    line-height: 48px;
    text-align: center;
    border-radius: 27px;
    transition: $transition;
    color: var(--orange-dark-clr);
    border: 1px solid var(--orange-dark-clr);
    &:hover {
      color: var(--white-clr);
      background-color: var(--orange-dark-clr);
    }
  }
  @include breakpoint(tablet) {
    h2 {
      font-size: 30px;
    }
    p {
      font-size: 1.25rem;
    }
  }
  > img {
    position: relative;
    bottom: -6px;
  }
}

/* End offer Section */

/* Start Footer section */
footer {
  @extend %footer;
}
/* End Footer section */

/* Start Animation */
@keyframes his {
  10% {
    transform: translateX(5px);
  }
  20% {
    transform: translateX(-5px);
  }
  30% {
    transform: translateX(4px);
  }
  40% {
    transform: translateX(-4px);
  }
  50% {
    transform: translateX(3px);
  }
  60% {
    transform: translateX(-3px);
  }
  70% {
    transform: translateX(2px);
  }
  80% {
    transform: translateX(-2px);
  }
  10% {
    transform: translateX(1px);
  }
  100% {
    transform: translateX(0px);
  }
}


@keyframes pulse {
  0% {
    transform: scale(1.3)
  }

  20% {
    transform: scale(2);
    opacity: 0.5;
  }

  60% {
    transform: scale(1);
    opacity: 0
  }
}
@keyframes scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes round {
  50%{
    transform: translateX(80px) rotate(180deg);
  }
  100% {
    transform: translateX(0) rotate(360deg);
  }
}

/* End Animation */
