:root {
  --testing-clr: #c5c5cc8a;
  --primary-clr: #FF9122;
  --primary-dark-clr: #F09872;
  --primary-light-clr: #FFB973;
  --white-clr: #fff;
  --white-light-clr: #f2f2f2;
  --white-dark-clr: #ffffffb8;
  --transparent-white-clr: #ffffff3b;
  --dark-clr: #1C2124;
  --dark-black-clr: #161A1D;
  --light-dark-clr: #33383A;
  --dark-light-clr: #4B5254;
  --black-clr: #000;
  --black-dark-clr: #222;
  --black-light-clr: #33383A;
  --transparent-black-clr: #0000001a;
  --blue-light-clr: #7C8E9A;
  --blue-dark-clr: #0059B2;
  --green-light-clr: #7ec062;
  --orange-light-clr: #fdd365;
  --orange-dark-clr: #ffd24d;
  --green-dark-clr: #128C7E;
  --red-light-clr: #e2494c;
  --gray-clr: #F0F4F5;
  --gray-light-clr: #B9B9D9;
  --gray-dark-clr: #646573;
  --purple-clr: #33335D;
  --purple-light-clr: #535362;
  --purple-dark-clr: #7555B7;
}

/* ------------- Project's sections' Table ----------------  

Project's sections' Table:
1 >> Header 
2 >> Landing 
3 >> Features
4 >> Services  
5 >> Portfolio  
6 >> About  
7 >> Contact  
8 >> Footer 

