/* ------------------- Start Global Rules ------------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

body::-webkit-scrollbar-track {
  background-color: var(--gray-clr);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--primary-clr);
  border-radius: 10px;
}

body::-webkit-scrollbar-corner {
  background-color: var(--primary-clr);
  border-radius: 10px;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
@media (min-width: 1400px) {
  .container {
    width: 1370px;
  }
}

video {
  max-width: 100%;
  height: auto;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}

/* ------------ End Global Rules --------------*/
/* ----------- Start Typography Rules ------------*/
body {
  line-height: 1.5;
  font-family: "open Sans", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 1200px) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* ---------- End Typography Rules -------------*/
/* ---------- Start header Section -------------*/
.contact h2, .features h2 {
  position: relative;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  text-transform: uppercase;
  color: var(--purple-clr);
}
.contact h2::before, .features h2::before {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 5px;
  transition: 0.5s;
  border-radius: 5px;
  background-color: var(--gray-clr);
}
.contact h2:hover::before, .features h2:hover::before {
  width: 100%;
}

.about-us h2, .office h2, .our-work h2 {
  text-align: center;
  text-transform: uppercase;
  color: var(--purple-clr);
}
.about-us h2 span, .office h2 span, .our-work h2 span {
  font-weight: normal;
}
.about-us h2 img, .office h2 img, .our-work h2 img {
  display: block;
  margin: 5px auto;
}

header {
  position: absolute;
  width: 100%;
  font-family: "Oswald", sans-serif;
}
header .toggle-btn {
  position: absolute;
  top: 3.125rem;
  cursor: pointer;
  z-index: 4;
}
@media (min-width: 992px) {
  header .toggle-btn {
    display: none;
  }
}
header .toggle-btn span {
  display: block;
  height: 0.125rem;
  width: 1.375rem;
  transition: 0.5s;
  background-color: var(--white-clr);
}
header .toggle-btn span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
header .toggle-btn:hover span:first-child {
  transform: translateY(-0.2rem);
}
header .toggle-btn:hover span:last-child {
  transform: translateY(0.2rem);
}
header .aside-menu {
  position: fixed;
  top: 0;
  left: -100vw;
  z-index: 11;
  min-height: 100vh;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  transition: 0.5s;
  width: calc(100vw - 1.5rem);
  max-width: 400px;
  background-image: url("../images/mobile-bg.jpg");
  background-size: cover;
}
header .aside-menu::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--transparent-white-clr);
}
@media (min-width: 992px) {
  header .aside-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: static;
    width: 100% !important;
    max-width: 100%;
    min-height: 5.625rem;
    left: 0;
    background-image: none;
  }
  header .aside-menu::after {
    display: none;
  }
}
header .aside-menu .header {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--transparent-black-clr);
  padding: 0.625rem 1.25rem;
}
header .aside-menu .header .close-btn {
  position: relative;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
header .aside-menu .header .close-btn:hover::after, header .aside-menu .header .close-btn:hover::before {
  background-color: var(--red-light-clr);
}
header .aside-menu .header .close-btn:before, header .aside-menu .header .close-btn:after {
  content: "";
  position: absolute;
  top: 50%;
  width: 100%;
  height: 2px;
  transition: 0.5s;
  transform-origin: center center;
  background-color: var(--black-clr);
}
header .aside-menu .header .close-btn:before {
  transform: rotate(45deg);
}
header .aside-menu .header .close-btn:after {
  transform: rotate(-45deg);
}
@media (min-width: 992px) {
  header .aside-menu .header {
    display: none;
  }
}
header .aside-menu .navbar {
  position: relative;
  z-index: 2;
}
@media (min-width: 992px) {
  header .aside-menu .navbar {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}
header .aside-menu .navbar .nav-item {
  display: block;
}
header .aside-menu .navbar .nav-item .nav-link {
  display: block;
  width: 100%;
  user-select: none;
  cursor: pointer;
  padding: 1.25rem;
  transition: color 0.5s;
  color: var(--black-clr);
}
header .aside-menu .navbar .nav-item .nav-link:hover {
  background-color: var(--white-dark-clr);
}
@media (min-width: 992px) {
  header .aside-menu .navbar .nav-item .nav-link {
    padding-top: 0;
    padding-bottom: 0;
    color: var(--white-clr);
  }
  header .aside-menu .navbar .nav-item .nav-link:hover {
    background-color: transparent;
  }
}
header .aside-menu .navbar .nav-item .mega-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .aside-menu .navbar .nav-item .mega-menu svg {
  font-size: 10px;
  margin-left: 5px;
  margin-top: 4px;
  transition: 0.5s;
}
header .aside-menu .navbar .sub-navbar {
  display: none;
  transition: 0.5s;
}
header .aside-menu .navbar .sub-navbar.active {
  display: block;
}
header .aside-menu .navbar .sub-navbar .sub-nav-item {
  display: block;
}
header .aside-menu .navbar .sub-navbar .sub-nav-item .sub-nav-link {
  display: block;
  width: 100%;
  font-size: 0.875rem;
  font-weight: normal;
  text-transform: capitalize;
  transition: 0.5s;
  border-left: 3px solid var(--gray-light-clr);
  padding: 1.25rem 3.125rem;
  color: var(--black-clr);
}
header .aside-menu .navbar .sub-navbar .sub-nav-item .sub-nav-link:hover {
  border-left-color: var(--primary-clr);
  color: var(--primary-clr);
}
@media (min-width: 992px) {
  header .aside-menu .navbar .sub-navbar .sub-nav-item .sub-nav-link {
    background-color: var(--white-clr);
    padding: 0.625rem;
  }
}
@media (min-width: 992px) {
  header .aside-menu .navbar .sub-navbar {
    position: absolute;
    width: 270px;
    padding: 0.625rem 3.125rem 0.625rem 0.625rem;
  }
  header .aside-menu .navbar .sub-navbar::after {
    content: "";
    position: absolute;
    top: -10px;
    left: 20px;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-bottom-color: var(--white-clr);
  }
}
header .phone {
  position: relative;
  z-index: 2;
  font-size: 1.125rem;
  letter-spacing: -1.5px;
  padding: 1.25rem;
}
header .phone svg {
  padding-right: 0.625rem;
}
@media (min-width: 992px) {
  header .phone {
    font-size: 1rem;
    padding: 0;
    color: var(--white-clr);
  }
}
header .phone:hover svg {
  animation: his 0.8s ease-in-out alternate;
}
header .whatsapp {
  display: block;
  position: relative;
  font-weight: bold;
  transition: 0.5s;
  z-index: 2;
  color: var(--green-dark-clr);
  padding: 0.625rem;
}
header .whatsapp svg {
  position: relative;
  bottom: -3px;
  font-size: 1.25rem;
  padding-right: 0.625rem;
}
@media (min-width: 992px) {
  header .whatsapp {
    position: fixed;
    bottom: 0.5rem;
    right: 1rem;
    font-size: 0.75rem;
    font-weight: normal;
    padding: 0.625rem;
    border-radius: 5px;
    color: var(--white-clr);
    background-color: var(--green-dark-clr);
  }
  header .whatsapp:hover {
    background-color: var(--primary-clr);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  z-index: 10;
  cursor: pointer;
  background-color: var(--black-clr);
}

footer {
  padding-top: 1.25rem;
  color: var(--dark-light-clr);
  background-color: var(--dark-clr);
}
@media (min-width: 992px) {
  footer .container {
    display: grid;
    grid-template-columns: 23% 35% 35%;
    column-gap: 7%;
  }
}
footer a {
  transition: 0.5s;
  color: var(--dark-light-clr);
}
footer a:hover {
  color: unset !important;
}
footer h4 {
  width: fit-content;
  text-transform: capitalize;
  margin-bottom: 1.25rem;
  color: var(--light-dark-clr);
  border-top: 2px solid var(--black-light-clr);
  border-bottom: 2px solid var(--black-light-clr);
}
footer .social-media {
  padding-bottom: 3.125rem;
}
footer .social-media p {
  max-width: 265px;
  padding-top: 0.625rem;
  padding-bottom: 1.25rem;
}
footer .social-media .social-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 250px;
}
footer .social-media .social-icons a > img {
  filter: grayscale(100%);
  transition: 0.5s;
}
footer .social-media .social-icons a > img:hover {
  filter: grayscale(0%);
}
footer .contact-info {
  padding-bottom: 3.125rem;
}
footer .contact-info div {
  display: flex;
  align-items: center;
  padding: 0.625rem 0;
}
footer .contact-info div svg {
  font-size: 25px;
  padding-right: 15px;
}
footer .blogs a {
  display: inline-block;
  min-height: 50px;
}
footer .blogs a:not(:last-child) {
  border-bottom: 2px solid var(--black-light-clr);
}
footer .blogs a img {
  display: inline;
  float: left;
  height: 40px;
  width: 65px;
  margin: 10px 10px 10px 0;
}
footer .blogs a span {
  vertical-align: middle;
}
footer .copyright {
  position: relative;
  font-size: 14px;
  text-align: center;
  padding: 1.25rem 0;
  background-color: var(--dark-black-clr);
}
@media (min-width: 992px) {
  footer .copyright {
    font-size: 16px;
  }
}
footer .copyright img {
  position: absolute;
  top: -17px;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 992px) {
  footer .contact-info,
footer .blogs {
    position: relative;
  }
  footer .contact-info::before,
footer .blogs::before {
    content: "";
    position: absolute;
    top: 40px;
    left: -20px;
    width: 2px;
    height: 72%;
    background-color: var(--black-light-clr);
  }
  footer .contact-info::after, footer .blogs::after,
footer .contact-info div:last-of-type::after, footer .blogs div::after {
    content: "";
    position: absolute;
    top: 30px;
    left: -24px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: var(--black-light-clr);
  }
  footer .contact-info div:last-of-type::after,
footer .blogs div::after {
    top: unset;
    bottom: 30px;
  }
}

/* End header */
/* Start Landing Section */
.landing {
  position: relative;
  display: block;
  height: 380px;
  background-image: url("../images/mobile-landing.png");
  background-size: cover;
  background-position: top center;
}
@media (min-width: 992px) {
  .landing {
    min-height: 100vh;
    background-image: url("../images/landing.png");
  }
}
.landing img {
  display: none;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -55%);
}
@media (min-width: 992px) {
  .landing img {
    display: block;
  }
}

/* End Landing Section */
/* Start Contact Section */
.contact {
  padding-top: 3.125rem;
  text-align: center;
}
@media (min-width: 992px) {
  .contact {
    background-image: url("../images/contact-bg.png");
    background-position: top center;
    background-size: cover;
    height: 497px;
    position: relative;
    top: -115px;
  }
}
.contact p {
  font-size: 0.875rem;
  padding-top: 1.25rem;
  color: var(--gray-light-clr);
}
.contact p span {
  display: none;
}
.contact .social-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1.25rem auto;
  gap: 0.625rem;
  max-width: 600px;
}
.contact .social-icons a {
  width: 3rem;
  height: 3rem;
}
.contact .social-icons a img {
  transition: 0.5s;
}
.contact .social-icons a:hover img {
  transform: translateY(-0.5rem);
  filter: saturate(1.5);
}
@media (min-width: 768px) {
  .contact p {
    font-size: 1rem;
  }
  .contact p span {
    display: inline-block;
  }
  .contact .social-icons a {
    width: 4rem;
    height: 4em;
  }
}

/* End Contact Section */
/* Start features Section */
.features {
  padding-top: 3.125rem;
  padding-bottom: 3.125rem;
}
@media (min-width: 992px) {
  .features {
    padding-top: 0;
  }
}
.features .features-list {
  display: grid;
  gap: 3.125rem;
  margin-top: 3.125rem;
}
@media (min-width: 992px) {
  .features .features-list {
    grid-template-columns: repeat(3, minmax(275px, 1fr));
  }
}
.features .features-list .feature-item {
  position: relative;
  text-align: center;
  padding: 3.125rem 1.25rem;
  border-radius: 10px;
  border: 1px solid var(--transparent-black-clr);
  box-shadow: 0px 0px 20px rgba(152, 152, 152, 0.33);
}
.features .features-list .feature-item:hover {
  border-color: var(--primary-light-clr);
  background-color: var(--primary-light-clr);
}
.features .features-list .feature-item:hover img:first-child {
  animation: scale 0.4s ease-in-out alternate;
}
.features .features-list .feature-item:hover img {
  filter: grayscale(0);
}
.features .features-list .feature-item:hover h3, .features .features-list .feature-item:hover p {
  color: var(--white-clr);
}
.features .features-list .feature-item img {
  filter: grayscale(100%);
}
.features .features-list .feature-item img:first-child {
  margin-bottom: 0.625rem;
}
.features .features-list .feature-item img:last-child {
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
}
.features .features-list .feature-item h3 {
  padding-bottom: 1.25rem;
  text-transform: capitalize;
  color: var(--purple-dark-clr);
}
.features .features-list .feature-item p {
  font-size: 0.875rem;
  line-height: 2;
  color: var(--gray-dark-clr);
}

/* End features Section */
/* Start Video Section */
.video {
  padding-top: 3.125rem;
  padding-bottom: 3.125rem;
  background-image: url("../images/video-bg.png");
  background-position: top center;
  background-size: cover;
  min-height: 790px;
}
.video .container {
  position: relative;
  padding-top: 3.125rem;
  padding-bottom: 3.125rem;
}
.video .container .img-holder {
  position: relative;
  top: 20px;
  height: 360px;
  border-radius: 10px;
  overflow: hidden;
  background-image: url("../images/video.png");
  background-position: top center;
  background-size: cover;
}
@media (min-width: 992px) {
  .video .container .img-holder {
    max-width: 450px;
    top: 0;
    left: 3.125rem;
    z-index: 2;
  }
}
.video .container .img-holder .video-play-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, -50%);
}
.video .container .img-holder .video-play-icon::after {
  content: "";
  left: 0;
}
.video .container .img-holder .video-play-icon:before, .video .container .img-holder .video-play-icon::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  z-index: 1;
  cursor: pointer;
  border-radius: 50%;
  animation: pulse 3s infinite;
  background-color: var(--transparent-white-clr);
}
.video .container .img-holder .video-play-icon::after {
  animation-delay: 0.4s;
}
.video .container .img-holder svg {
  cursor: pointer;
  border-radius: 50%;
  padding: 1.125rem 1.25rem;
  color: var(--primary-dark-clr);
  background-color: var(--white-clr);
}
.video .container .text {
  text-align: center;
  padding: 3.125rem 0 1.25rem;
  border-radius: 10px;
  background-color: var(--primary-dark-clr);
}
@media (min-width: 992px) {
  .video .container .text {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%);
    text-align: right;
    padding: 4.375rem 3.125rem;
  }
}
@media (min-width: 1200px) {
  .video .container .text {
    padding: 4.375rem 6.25rem;
  }
}
.video .container .text p {
  display: inline-block;
  font-size: 0.875rem;
  letter-spacing: -0.5px;
  color: var(--white-clr);
}
@media (min-width: 768px) {
  .video .container .text p {
    font-size: 20px;
  }
}
@media (min-width: 1200px) {
  .video .container .text p {
    font-size: 25px;
  }
}
.video .container .text img {
  display: inline-block;
  transition: 0.5s;
  object-fit: cover;
}
@media (min-width: 1200px) {
  .video .container .text:hover img {
    animation: round 2s ease-in-out alternate;
  }
}
.video .container video {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 90%;
  z-index: 12;
  border-radius: 5px;
}
@media (min-width: 992px) {
  .video .container video {
    max-width: 50%;
  }
}

/* End Video Section */
/* Start About Us Section */
.about-us {
  position: relative;
  padding-top: 3.125rem;
  padding-bottom: 3.125rem;
}
@media (min-width: 992px) {
  .about-us {
    margin-bottom: 300px;
  }
  .about-us::before {
    content: "";
    position: absolute;
    top: 150px;
    left: 50%;
    transform: translateX(-50%);
    width: 3px;
    height: 115%;
    background: linear-gradient(to bottom, var(--orange-light-clr), transparent);
  }
  .about-us::after {
    content: "";
    position: absolute;
    bottom: -280px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 47%;
    margin-left: 0.5px;
    border: 1px dashed var(--orange-light-clr);
  }
  .about-us .container {
    position: relative;
  }
  .about-us .container::before {
    content: "";
    position: absolute;
    top: 90px;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
