/* Start Animation */
@keyframes his {
  10% {
    transform: translateX(5px);
  }
  20% {
    transform: translateX(-5px);
  }
  30% {
    transform: translateX(4px);
  }
  40% {
    transform: translateX(-4px);
  }
  50% {
    transform: translateX(3px);
  }
  60% {
    transform: translateX(-3px);
  }
  70% {
    transform: translateX(2px);
  }
  80% {
    transform: translateX(-2px);
  }
  10% {
    transform: translateX(1px);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes pulse {
  0% {
    transform: scale(1.3);
  }
  20% {
    transform: scale(2);
    opacity: 0.5;
  }
  60% {
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes round {
  50% {
    transform: translateX(80px) rotate(180deg);
  }
  100% {
    transform: translateX(0) rotate(360deg);
  }
}
/* End Animation */