// Here you can create and customize the pages' footer:

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;


%footer {
  padding-top: $txt-padding-md;
  color: var(--dark-light-clr);
  background-color: var(--dark-clr);
  .container {
    @include breakpoint(laptop) {
      display: grid;
      grid-template-columns: 23% 35% 35%;
      column-gap: 7%;
    }
  }
  a {
    transition: $transition;
    color: var(--dark-light-clr);
    &:hover {
      color: unset !important;
    }
  }
  h4 {
    width: fit-content;
    text-transform: capitalize;
    margin-bottom: $txt-padding-md;
    color: var(--light-dark-clr);
    border-top: 2px solid var(--black-light-clr);
    border-bottom: 2px solid var(--black-light-clr);
  }
    .social-media {
      padding-bottom : $txt-padding-lg;    
      p {
        max-width: 265px;
        padding-top: $txt-padding-sm;
        padding-bottom : $txt-padding-md;

      }
      .social-icons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 250px;
        a > img {
          filter: grayscale(100%);
          transition: $transition;
          &:hover {
            filter: grayscale(0%);
          }
        }
    }
  }
  .contact-info {
    padding-bottom: $txt-padding-lg;
    div {
      display: flex;
      align-items: center;
      padding: $txt-padding-sm 0;
      svg {
        font-size: 25px;
        padding-right: 15px;
      }
    }
  }
  .blogs {
    a {
      display: inline-block;
      min-height: 50px;
      &:not(:last-child) {
        border-bottom: 2px solid var(--black-light-clr);
      }
      img {
        display: inline;
        float: left;
        height: 40px;
        width: 65px;
        margin: 10px 10px 10px 0;
      }
      span {
        vertical-align: middle;
      }
    }
  }
  .copyright {
    position: relative;
    font-size: 14px;
    text-align: center;
    padding: $txt-padding-md 0;
    background-color: var(--dark-black-clr);
    @include breakpoint(laptop) {
      font-size: 16px;
    }
    img {
      position: absolute;
      top: -17px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  @include breakpoint(laptop) {
  .contact-info,
  .blogs {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 40px;
      left: -20px;
      width: 2px;
      height: 72%;
      background-color: var(--black-light-clr);
    }
    }
    .contact-info, .blogs,
    .contact-info div:last-of-type, .blogs div {
      &::after {
          content: '';
          position: absolute;
          top: 30px;
          left: -24px;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background-color: var(--black-light-clr);
        }
    }
        .contact-info div:last-of-type,
        .blogs div {
          &::after {
            top: unset;
            bottom: 30px;
          }
        }
  }
}

